import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Login2Page } from "../login2/login2";

import { PublicAvisoPage  } from "../public-aviso/public-aviso";
//import { AboutPage } from '../about/about';
//import { LoginPage } from "../login/login";

@IonicPage()
@Component({
  selector: 'page-before-cuenta2',
  templateUrl: 'before-cuenta2.html',
})
export class BeforeCuenta2Page {

constructor(public navCtrl: NavController, public navParams: NavParams) {
}

ionViewDidLoad() {
  //console.log('ionViewDidLoad VcuentaPage');
}

openPage(sw){
  if (sw == 1) {
    this.navCtrl.push(Login2Page);
  }
  else {
    this.navCtrl.push(PublicAvisoPage);
  }
}

}



