import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Login1Page } from "../login1/login1";
import { PublicPerfilPage } from "../public-perfil/public-perfil";
import { Cuenta1Page } from "../cuenta1/cuenta1";
@IonicPage()
@Component({
  selector: 'page-before-cuenta1',
  templateUrl: 'before-cuenta1.html',
})
export class BeforeCuenta1Page {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad BeforeCuenta1Page');
  }
  openPage(sw) {
    if (sw == 1) {
      this.navCtrl.push(Login1Page);
    }
    else {
      this.navCtrl.push(PublicPerfilPage);
    }
  }


ingresar() {
  this.navCtrl.setRoot(Cuenta1Page);
}


}



