import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Cuenta2Page } from "../cuenta2/cuenta2";

@IonicPage()
@Component({
  selector: 'page-login2',
  templateUrl: 'login2.html',
})
export class Login2Page {
  tab:any;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  this.tab=document.querySelector('.tabbar.show-tabbar');
  }

  ionViewWillEnter(){
    this.tab.style.display='none';
  }
  ionViewWillLeave(){
    this.tab.style.display = 'flex';
  }



  ionViewDidLoad() {
  //  console.log('ionViewDidLoad Login2Page');
  }

  ingresar() {
    this.navCtrl.setRoot(Cuenta2Page);
  }



}



