import { Component } from '@angular/core';
import { ModalController, IonicPage, NavController, NavParams } from 'ionic-angular';

//import { BeforeCuenta1Page } from "../before-cuenta1/before-cuenta1";
import { VpreviaPerfilPage } from "../vprevia-perfil/vprevia-perfil";

@IonicPage()
@Component({
  selector: 'page-public-perfil',
  templateUrl: 'public-perfil.html',
})
export class PublicPerfilPage {
dts_person={ 
  nom:String,
  apell:String,
  ci:Number,
  expedido: String,
  fechaNac:Date,
  lugarNac:String,
  celular:Number,
  direccion_1:String,
  ciudad_actual: String,
  foto:String
};

  tabBarElement: any;
  constructor(public modalCtrl: ModalController, public navCtrl: NavController, public navParams: NavParams) {

    this.tabBarElement = document.querySelector('.tabbar.show-tabbar');
  
  }

  ionViewDidLoad() {
   // console.log('ionViewDidLoad PublicPerfilPage');
  }

  goBack() {
    this.navCtrl.pop();
  }
  presentModal() {
    let modal = this.modalCtrl.create(VpreviaPerfilPage);
    modal.present();
  }


  /* OCULTAR TAB */

ionViewWillEnter() {
  this.tabBarElement.style.display = 'none';
}

ionViewWillLeave() {
  this.tabBarElement.style.display = 'flex';
}


  addExperiencia(){
   var text = document.getElementById('add').innerText;
    console.log(text);
    if (text =="ELIMINAR") {
      document.getElementById('add').style.background = "#00aeef";
      document.getElementById('add').innerText = 'AGREGAR';      
   }
   else{
      document.getElementById('add').style.background = "#f53d3d";
      document.getElementById('add').innerText = 'ELIMINAR';      
   }
    
  }


}



