import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';


import { MyApp } from './app.component';
//import { HomePage } from '../pages/home/home';

/* paginas de candidato */
import { Alerta1Page } from "../pages/candidato/alerta1/alerta1";
import { BeforeCuenta1Page } from "../pages/candidato/before-cuenta1/before-cuenta1";
import { Buscar1Page  } from "../pages/candidato/buscar1/buscar1";
import { Cuenta1Page } from "../pages/candidato/cuenta1/cuenta1";
import { Editar1Page } from "../pages/candidato/editar1/editar1";
import { Inicio1Page } from "../pages/candidato/inicio1/inicio1";
import { Login1Page } from "../pages/candidato/login1/login1";
import { PublicPerfilPage } from "../pages/candidato/public-perfil/public-perfil";
import { Tab1Page  } from "../pages/candidato/tab1/tab1";
import { Terminos1Page } from "../pages/candidato/terminos1/terminos1";
import { VpreviaPerfilPage } from "../pages/candidato/vprevia-perfil/vprevia-perfil";




/* paginas de cliente */
import { Tab2Page } from "../pages/cliente/tab2/tab2";
import { Buscar2Page } from "../pages/cliente/buscar2/buscar2";
import { Inicio2Page } from "../pages/cliente/inicio2/inicio2";

import { Alerta2Page  } from "../pages/cliente/alerta2/alerta2";
import { BeforeCuenta2Page } from "../pages/cliente/before-cuenta2/before-cuenta2";
import { Cuenta2Page } from "../pages/cliente/cuenta2/cuenta2";

import { PublicAvisoPage  } from "../pages/cliente/public-aviso/public-aviso";
import { Editar2Page } from "../pages/cliente/editar2/editar2";
import { Login2Page } from "../pages/cliente/login2/login2";
import { Pago2Page } from "../pages/cliente/pago2/pago2";
import { BeforePublicPage } from "../pages/cliente/before-public/before-public";

import { DataProvider } from '../providers/data/data';
import { AvisosProvider } from '../providers/avisos/avisos';
import { HttpClientModule } from "@angular/common/http";



@NgModule({
  declarations: [
    MyApp,
    Tab1Page,

Tab2Page,
Buscar2Page,
Inicio2Page,
Alerta2Page,
BeforeCuenta2Page,
Cuenta2Page,
PublicAvisoPage,
Editar2Page,
Login2Page,
Pago2Page,
BeforePublicPage,


Alerta1Page,
BeforeCuenta1Page,
Buscar1Page,
Cuenta1Page,
Editar1Page,
Inicio1Page,
Login1Page,
PublicPerfilPage,
Terminos1Page,
VpreviaPerfilPage

   
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    
    IonicModule.forRoot(MyApp, {}, {
      links: [
        { segment: 'candidato', component: Tab1Page, name: 'Inicio Candidato' },
        { segment: 'cliente', component: Tab2Page, name: 'Inicio Cliente' }
      ]
    }
  
  )
  ],
  bootstrap: [IonicApp],
  
  entryComponents: [
    MyApp,
    Tab1Page,
    
  
    Tab2Page,
    Buscar2Page,
    Inicio2Page,
    Alerta2Page,
    BeforeCuenta2Page,
    Cuenta2Page,
    PublicAvisoPage,
    Editar2Page,
    Login2Page,
    Pago2Page,
    BeforePublicPage,
  
    Alerta1Page,
    BeforeCuenta1Page,
    Buscar1Page,
    Cuenta1Page,
    Editar1Page,
    Inicio1Page,
    Login1Page,
    PublicPerfilPage,
    Terminos1Page,
    VpreviaPerfilPage

    
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    DataProvider,
    AvisosProvider
  ]
})
export class AppModule {}



