import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { Pago2Page  } from "../pago2/pago2";

@Component({
  selector: 'page-buscar2',
  templateUrl: 'buscar2.html',
})
export class Buscar2Page {
  check: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, private toastCtrl: ToastController) {
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad CandidatosPage');
  }

  comprar(c) {

    //alert("ENVIA AL FORMULARIO DE COMPRA DE PERFILES");
    this.navCtrl.push(Pago2Page);

  }

  updateCheck() {
    let msj = 'Se Añadio a la Lista para descargar';
    if (!this.check) {
      msj = 'Se Quito de la Lista';
    }
    let toast = this.toastCtrl.create({
      message: msj,
      duration: 1000,
      position: 'top'
    });

    toast.onDidDismiss(() => {
      console.log('Dismissed toast');
    });

    toast.present();
  }

}



